.rc-steps {
  font-size: 0;
  width: 100%;
  line-height: 1.5;
  display: flex;
}
.rc-steps,
.rc-steps * {
  box-sizing: border-box;
}
.rc-steps-item {
  position: relative;
  display: inline-block;
  vertical-align: top;
  flex: 1;
  overflow: hidden;
  &:last-child {
    flex: none;
  }
  &.rc-steps-next-error {
    .rc-steps-item-title {
      &:after {
        background: #f50;
      }
    }
  }
}
.rc-steps-item-container[role="button"] {
  cursor: pointer;
  transition: opacity 0.3s;
}
.rc-steps-item-container[role="button"]:hover {
  opacity: 0.7;
}
.rc-steps-item:last-child .rc-steps-item-tail,
.rc-steps-item:last-child .rc-steps-item-title:after {
  display: none;
}
.rc-steps-item-container {
  display: inline-block;
}
.rc-steps-item-icon,
.rc-steps-item-content {
  display: inline-block;
  vertical-align: top;
}
.rc-steps-item-icon {
  border: 1px solid #ccc;
  width: 26px;
  height: 26px;
  line-height: 26px;
  text-align: center;
  border-radius: 26px;
  font-size: 14px;
  margin-right: 8px;
  transition: background-color 0.3s, border-color 0.3s;
  & > .rc-steps-icon {
    line-height: 1;
    top: -1px;
    color: #108ee9;
    position: relative;
    &.rcicon {
      font-size: 12px;
      position: relative;
      top: -2px;
    }
  }
}
.rc-steps-item-tail {
  position: absolute;
  left: 0;
  width: 100%;
  top: 12px;
  padding: 0 10px;
  &:after {
    content: "";
    display: inline-block;
    background: #e9e9e9;
    height: 1px;
    border-radius: 1px;
    width: 100%;
    transition: background 0.3s;
  }
}
.rc-steps-item-content {
  margin-top: 3px;
}
.rc-steps-item-title {
  font-size: 14px;
  margin-bottom: 4px;
  color: #666;
  font-weight: bold;
  display: inline-block;
  padding-right: 10px;
  position: relative;
  &:after {
    content: "";
    height: 1px;
    width: 1000px;
    background: #e9e9e9;
    display: block;
    position: absolute;
    top: 0.55em;
    left: 100%;
  }
}
.rc-steps-item-subtitle {
  font-size: 12px;
  display: inline-block;
  color: #999;
  margin-left: 8px;
}
.rc-steps-item-description {
  font-size: 12px;
  color: #999;
}
.rc-steps-item-wait {
  .rc-steps-item-icon {
    border-color: #ccc;
    background-color: #fff;
    & > .rc-steps-icon {
      color: #ccc;
      .rc-steps-icon-dot {
        background: #ccc;
      }
    }
  }
  .rc-steps-item-title {
    color: rgba(0, 0, 0, 0.43);
    &:after {
      background-color: #e9e9e9;
    }
  }
  .rc-steps-item-description {
    color: rgba(0, 0, 0, 0.43);
  }
  .rc-steps-item-tail {
    &:after {
      background-color: #e9e9e9;
    }
  }
}
.rc-steps-item-process {
  .rc-steps-item-icon {
    border-color: #108ee9;
    background-color: #fff;
    background: #108ee9;
    & > .rc-steps-icon {
      color: #108ee9;
      color: #fff;
      .rc-steps-icon-dot {
        background: #108ee9;
      }
    }
  }
  .rc-steps-item-title {
    color: rgba(0, 0, 0, 0.65);
    &:after {
      background-color: #e9e9e9;
    }
  }
  .rc-steps-item-description {
    color: rgba(0, 0, 0, 0.65);
  }
  .rc-steps-item-tail {
    &:after {
      background-color: #e9e9e9;
    }
  }
}
.rc-steps-item-finish {
  .rc-steps-item-icon {
    border-color: #108ee9;
    background-color: #fff;
    & > .rc-steps-icon {
      color: #108ee9;
      .rc-steps-icon-dot {
        background: #108ee9;
      }
    }
  }
  .rc-steps-item-title {
    color: rgba(0, 0, 0, 0.43);
    &:after {
      background-color: #108ee9;
    }
  }
  .rc-steps-item-description {
    color: rgba(0, 0, 0, 0.43);
  }
  .rc-steps-item-tail {
    &:after {
      background-color: #108ee9;
    }
  }
}
.rc-steps-item-error {
  .rc-steps-item-icon {
    border-color: #f50;
    background-color: #fff;
    & > .rc-steps-icon {
      color: #f50;
      .rc-steps-icon-dot {
        background: #f50;
      }
    }
  }
  .rc-steps-item-title {
    color: #f50;
    &:after {
      background-color: #e9e9e9;
    }
  }
  .rc-steps-item-description {
    color: #f50;
  }
  .rc-steps-item-tail {
    &:after {
      background-color: #e9e9e9;
    }
  }
}
.rc-steps-horizontal {
  &:not(.rc-steps-label-vertical) {
    .rc-steps-item {
      margin-right: 10px;
      &:last-child {
        margin-right: 0;
      }
    }
    .rc-steps-item-tail {
      display: none;
    }
    .rc-steps-item-description {
      max-width: 100px;
    }
  }
}
.rc-steps-vertical {
  display: block;
  .rc-steps-item {
    display: block;
    overflow: visible;
  }
  .rc-steps-item-icon {
    float: left;
  }
  .rc-steps-item-icon-inner {
    margin-right: 16px;
  }
  .rc-steps-item-content {
    min-height: 48px;
    overflow: hidden;
    display: block;
  }
  .rc-steps-item-title {
    line-height: 26px;
    &:after {
      display: none;
    }
  }
  .rc-steps-item-description {
    padding-bottom: 12px;
  }
  .rc-steps-item-tail {
    position: absolute;
    left: 13px;
    top: 0;
    height: 100%;
    width: 1px;
    padding: 30px 0 4px 0;
    &:after {
      height: 100%;
      width: 1px;
    }
  }
  &.rc-steps-small {
    .rc-steps-item-tail {
      position: absolute;
      left: 9px;
      top: 0;
      padding: 22px 0 4px 0;
    }
    .rc-steps-item-title {
      line-height: 18px;
    }
  }
}
